@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';
@import '../../../styles/variables';

$image-width: 359px;
$meta-width: 155px;

.Episode {
    position: relative;
    margin-bottom: $spacing-05;
    border-radius: $radius-03;
    overflow: hidden;
    width: $episode-width;


    .Link {
        display: block;
        height: $episode-height;
        padding-left: calc(#{$image-width} + #{$spacing-07});
        padding-right: calc(#{$meta-width} + #{$spacing-07});
        background-color: #{$color-itvx-neutrals-400}#{$opacity-01 * 100} ;
        position: relative;

        &.focus {
            background-color: #{$color-itvx-neutrals-400}#{$opacity-03 * 100} ;
        }
    }

    &__title {
        padding-top: $spacing-07;
        @include itv-font-type(subtitle-1, bold);
        z-index: 1;
    }

    &__subtitle {
        margin-top: $spacing-03;
        @include itv-font-type(subtitle-2);
        color: $color-itvx-neutrals-300;
    }

    &__image {
        width: $image-width;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;

        .cp_picture {
            height: 100%;
        }
    }

    &__image-front {
        position: absolute;
        bottom: 0;
        left: 0;
    }

    &__progress {
        padding: 0 $spacing-05 $spacing-04;
        width: calc(#{$image-width} - #{2 * $spacing-05});

        progress {
            display: block;
        }
    }

    &__premium-tag {
        margin-bottom: $spacing-04;
        margin-left: $spacing-05;
    }

    &__tag {
        bottom: 0;
        position: absolute;
    }

    &__description {
        @include text-wrap(body-small, 2);
        @include itv-font-type(body, regular);
        margin-top: $spacing-03;
        margin-bottom: $spacing-06;
        color: $color-itvx-neutrals-200;
        max-height: 60px;
        overflow: hidden;
    }

    &__meta-container {
        position: absolute;
        bottom: 0;
        right: 0;
        color: $color-itvx-text-secondary;
        @include itv-font-type(body-small);
        text-align: right;
        padding-right: $spacing-07;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    &__meta {
        margin-top: auto;
        padding-bottom: $spacing-06;

        > * {
            margin-top: $spacing-02;
        }
    }

    &__duration {
        &__clock-icon {
            width: 15px;
            height: 15px;
            margin-right: $spacing-03;
            position: relative;
            top: -2px;

            svg {
                path {
                    fill: $color-itvx-neutrals-100;
                }
            }
        }
    }

    &__days-left {
        svg {
            vertical-align: middle;
            position: relative;
            top: -1px;
            width: 18px;
            height: 18px;
        }
    }

    &__tags {
        margin-top: $spacing-07;
        color: $color-itvx-neutrals-100;
        
        div {
            margin-left: $spacing-03;
            height: 28px;
        }
    }
}