// Utilities
//
// Useful functions and mixins.

// Returns a rounded value with or without decimal
// decimal-round(0.333)    => 0
// decimal-round(0.333, 1) => 0.3
// decimal-round(0.333, 2) => 0.33
// decimal-round(0.666)    => 1
// decimal-round(0.666, 1) => 0.7
// decimal-round(0.666, 2) => 0.67

@use "sass:math";

@function decimal-round($number, $digits: 0, $mode: round) {
    $n: 1;
    // $number must be a number
    @if type-of($number) != number {
        @warn '#{ $number } is not a number.';
        @return $number;
    }
    // $digits must be a unitless number
    @if type-of($digits) != number {
        @warn '#{ $digits } is not a number.';
        @return $number;
    } @else if not unitless($digits) {
        @warn '#{ $digits } has a unit.';
        @return $number;
    }
    @if $digits > 0 {
        @for $i from 1 through $digits {
            $n: $n * 10;
        }
    }
    @if $mode == round {
        @return math.div(round($number * $n), $n);
    } @else if $mode == ceil {
        @return math.div(ceil($number * $n), $n);
    } @else if $mode == floor {
        @return math.div(floor($number * $n), $n);
    } @else {
        @warn '#{ $mode } is undefined keyword.';
        @return $number;
    }
}

// returns a specific property 1 level deeper
// Example: .myclass { min-height: map-deep-get($itv-breakpoints, sponge, breakpoint) / 1.8; }

@function map-deep-get($map, $category, $item) {
    @each $key, $value in $map {
        @if $key == $category {
            @return map-get($value, $item);
        }
    }
    @return false;
}

// Returns the value of a breakpoint-varying property at a certain breakpoint.
//
// $value            - The name of the value to return.
// $breakpoint       - Breakpoint at which to value is wanted.
// $breakpoints      - The map of breakpoints with their specific values.
//
// Compatibility untested.
@function itv-bp-value($value, $breakpoint: $itv-breakpoint, $breakpoints: $itv-breakpoints) {
    @return map-deep-get($breakpoints, $breakpoint, $value);
}

// Returns the index of a key in a map.
//
// $map              - The map.
// $key-to-find      - The key to find.
//
// Compatibility untested.
@function itv-map-key-index($map, $key-to-find) {
    $i: 0;
    @each $key, $value in $map {
        $i: $i + 1;
        @if $key == $key-to-find {
            @return $i;
        }
    }
    @return false;
}

// Remove the unit of a length
//
// $Number          - Number to remove unit from
//
// Compatibility untested.
@function strip-unit($number) {
    @if type-of($number) == 'number' and not unitless($number) {
        @return math.div($number, $number * 0 + 1);
    }

    @return $number;
}

// Given a numerator and a denominator, returns the highest common factor
//
// $n - The numerator
// $d - The denominator
//
// Compatibility untested.
@function itv-hcf($n, $d) {
    $hcf: 1;
    @for $f from 2 through $d {
        @if $n%$f == 0 and $d%$f == 0 {
            $hcf: $f;
        }
    }
    @return $hcf;
}

// Converts a fraction to a percentage.
//
// $n                - Fraction numerator.
// $d                - Fraction denominator.
//
// Compatibility untested.
@function itv-fraction-to-percentage($n, $d) {
    @return math.div($n, $d)*100%;
}

@function str-replace($string, $search, $replace: '') {
    $index: str-index($string, $search);
    @if $index {
        @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
    }
    @return $string;
}

$encoding-reference: (
    ('<','%3C'),
    ('>','%3E'),
    ('"','\''),
    ('#','%23'),
    ('&','%26')
);

@function svg-encode($svg) {
    @each $char, $encoded in $encoding-reference {
        $svg: str-replace($svg, $char, $encoded);
    }
    @return 'data:image/svg+xml,' + $svg;
}

// Provides a shorthand to style input fields placeholder text
//
// Compatibility untested.
@mixin placeholder {
    /* stylelint-disable selector-no-vendor-prefix -- required for setting placeholder styles */
    &::-moz-placeholder { @content; }
    &:-moz-placeholder { @content; }
    &::-webkit-input-placeholder { @content; }
    &:-ms-input-placeholder { @content; }
    /* stylelint-enable selector-no-vendor-prefix */
}

@function get-font-size($font-type: body, $limit: false) {
    @if $limit {
        @return map-get($itv-font-fluid-scale, $font-type, $limit);
    } 
    @return map-get($itv-font-fluid-scale, $font-type);
}

@function get-max-breakpoint() {
    @if $itv-platform == 'ctv' {
        @return 1920;
    } @else {
        @return 2560;
    }
}

@function get-min-breakpoint() {
    @return 320;
}

// Provides a shorthand for wrapping long text
//
// Compatibility untested.
@mixin text-wrap($font-type: body, $max-lines: 2) {
    $line-height: map-get($itv-font-fluid-scale, $font-type, height);
    $font-size: get-font-size($font-type, max);

    $base-font-size: get-font-size('base'); // used to convert to rem
    $min-font-size: math.div(get-font-size($font-type, min), $base-font-size);
    $max-font-size: math.div(get-font-size($font-type, max), $base-font-size);
    $max-breakpoint: math.div(get-max-breakpoint(), $base-font-size);
    $min-breakpoint: math.div(get-min-breakpoint(), $base-font-size);
    $max-height: calc((#{$min-font-size}rem +
    (#{$max-font-size} - #{$min-font-size}) *
    ((100vw - #{$min-breakpoint}rem) / (#{$max-breakpoint} - #{$min-breakpoint}))) * (#{$max-lines} * #{$line-height} + #{$min-font-size}));

    @if $itv-platform == 'ctv' {
        $max-height: #{($max-font-size * $line-height) * $max-lines}rem;
    }

    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $max-lines; /* number of lines to show */
    line-height: #{$line-height}em; /* fallback */
    max-height: $max-height;   /* fallback */
    white-space: normal;
}

@mixin dot($dimension, $color) {
    width: $dimension;
    height: $dimension;
    background: $color;
    border-radius: 50%;
}

// A map-merge function to merge multidimensional maps in a way that map data types contained within the merging maps also get merged.
@function recursive-map-merge($map1, $map2) {
    @if (type-of($map1) == map or (type-of($map1) == list and length($map1) == 0)) and (type-of($map2) == map or (type-of($map2) == list and length($map2) == 0)) {
        $result: $map1;
        @each $key, $value in $map2 {
            @if (type-of(map-get($result, $key)) == map and type-of($value) == map) {
                $result: map-merge($result, ($key: recursive-map-merge(map-get($result, $key), $value)));
            }
            @else {
                $result: map-merge($result, ($key: $value));
            }
        }
        @return $result;
    }
    @else {
        @warn "recursive-map-merge() expects it\'s parameters to be map types!";
        @return null;
    }
}

// Creates a grid wrapper applying padding values at a given breakpoint.
//
// $prefix                  - The prefix to use for the classes.
// $breakpoint              - The breakpoint to write the classes for.
//
// Compatibility untested.

@mixin itv-grid-wrapper($prefix: 'grid__wrapper', $breakpoint: $itv-base-breakpoint) {
    .#{$ns}#{$prefix},
    %#{$prefix} {
        $grid-wrapper: map-get($itv-breakpoints, $breakpoint, grid-wrapper);

        padding-left: #{map-get($grid-wrapper, left)};
        padding-right: #{map-get($grid-wrapper, right)};
    }
}
