@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/variables';

.ScrollableList {
    transform-origin: top;
    position: relative;

    &__row {
        opacity: $opacity-06;
        max-width: 70vw;
    }

    &__above {
        opacity: 0;
    }
}

.animations {
    .ScrollableList__animate {
        // Ease out to start quickly, giving a fast user interaction response.
        // It slows towards the end for smooth landing on the new tile.
        transition: transform $motion-duration-02 ease-out;
        transform-origin: top;
        height: $app-height;

        .ScrollableList__row {
            // This is used when navigating up to the slider above the current one.
            // The newly focused slider goes from zero opacity to one.
            // A slow start is used to keep the tile edges soft when they're coming
            // in from outside the slider area (bottom area of the letterbox).
            transition: opacity $motion-duration-02 ease-in;
            transform-origin: top;
        }

        .ScrollableList__above {
            // Used to fade out current row when navigating to the slider below.
            // It should fade out quickly to avoid the clash of the tiles against
            // the background of the letterbox.
            transition: opacity $motion-duration-01 ease-out;
            transform-origin: top;
        }
    }
}
