@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '~@itv/fe-components/cjs/common/styles/tokens/variables';

.Programme {
    $parent: &;
    transition: transform $motion-duration-06 $motion-easing-02;
    transform: translateY(0);

    &__scrollable-list-container {
        overflow: visible;
        position: relative;

        .CategorySlider {
            margin-top: $spacing-07;
        }
    }

    &__content {
        height: 100%;
        position: relative;
    }

    .animations &__hero {
        transition: opacity $motion-duration-06 $motion-easing-02;
    }

    .FocusFrame {
        &.is-slider {
            bottom: unset;
            left: 0;
            top: 216px;
            width: 248px;
        }
    }

    .is-banner-last {
        .FocusFrame {
            &.is-slider {
                top: 156px;
            }
        }
    }

    &__letterBoxMargin {
        margin-bottom: $spacing-06;
    }

    &__myList-button {
        display: inline-block;

        i {
            margin-right: $spacing-04;
        }
    }

    &__hero.FocusGroup--containsFocus {
        display: block;
        transform: translateY(0%);
    }

    &--hideHeader {
        transform: translateY(-761px);

        #{$parent}__hero {
            opacity: 0;
        }
    }
}
