@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../styles/common';

.EpisodesHeader {
    @include itv-font-type(subtitle-2);
    margin-bottom: $spacing-05;
    align-items: center;

    &__fullSeriesLabel {
        @include itv-font-type(tags);
        display: inline-block;
        color: $color-itvx-text-inverse;
        background-color: $color-itvx-accents-primary;
        padding: 0 $spacing-03;
        margin: $spacing-05;
    }

    .secondary {
        color: $color-itvx-neutrals-300;
    }
}

.FocusGroup--containsFocus + .EpisodesHeader {
    margin-top: $spacing-05;
}
